body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App h1 {
  text-align: center;
}

.Datatable {
  text-align: center;
}

.Datatable__select {
  margin-bottom: 10px;
  padding-left: 10px;
  width: 70px;
  text-align-last: center;
  -webkit-appearance: button;
}

.Datatable__select:focus,
.Datatable__pagination button:focus {
  outline: none;
}

.Datatable__pagination {
  margin-bottom: 20px;
}

.Datatable__pagination button {
  margin: 0 2px;
}

.Table {
  margin: 0 auto 50px;
  text-align: left;
  border-spacing: 0;
}

.Table th {
  border-bottom: 1px solid dimgray;
}

.Table th,
.Table td {
  padding: 5px 10px;
  border-left: 1px solid dimgray;
}

.Table th:last-child,
.Table td:last-child {
  border-right: 1px solid dimgray;
}


.Table td:nth-child(n + 3):nth-child(-n + 5) {
  text-align: center;
}

.Table__sort-up::after,
.Table__sort-down::after {
  color: red;
}

.Table__sort-up::after {
  content: '\25BC';
}

.Table__sort-down::after {
  content: '\25B2';
}
